




































import Vue from "vue";
import Component from "vue-class-component";

@Component
export default class SectionContact extends Vue {
  // METHODS
  private actionContact(menu: string) {
    const whatsappMessage = "Halo, saya tertarik dengan produk DagingTernak!";
    const whatsappURL = this.getLinkWhastapp("6289647329246", whatsappMessage);
    switch (menu) {
      case "whatsapp":
        window.open(whatsappURL, "_blank");
        break;
      case "gmail":
        window.open("mailto:dagingternak@gmail.com", "_blank");
        break;
      case "location":
        window.open("https://goo.gl/maps/AgpgDxFKiu1DwF5KA", "_blank");
        break;
      default:
        window.open("https://www.instagram.com/dagingternak", "_blank");
        break;
    }
  }

  private getLinkWhastapp(number: string, message: string) {
    var url =
      "https://api.whatsapp.com/send?phone=" +
      number +
      "&text=" +
      encodeURIComponent(message);

    return url;
  }
}
