










































































import Vue from "vue";
import $ from "jquery";
import Component from "vue-class-component";

@Component
export default class Navbar extends Vue {
  // METHODS
  private clickMenu(menu: string) {
    switch (menu) {
      case "product":
        $(".nav-link").removeClass("active");
        $("#menu-product").addClass("active");
        break;
      case "partner":
        $(".nav-link").removeClass("active");
        $("#menu-partner").addClass("active");
        break;
      case "contact":
        $(".nav-link").removeClass("active");
        $("#menu-contact").addClass("active");
        break;
      case "investor":
        $(".nav-link").removeClass("active");
        this.$router.push({ name: "Investor" });
        break;
      case "news":
        $(".nav-link").removeClass("active");
        this.$router.push({ name: "News" });
        break;
      default:
        if (this.$route.name === "Home") {
          $("#menu-home").addClass("active");
        }
        break;
    }
  }

  private created() {
    const route = this.$route;
    if (route.hash) {
      setTimeout(() => {
        this.clickMenu(route.hash.replaceAll("#", ""));
      }, 1000);
    } else {
      setTimeout(() => {
        this.clickMenu("home");
      }, 1000);
    }
  }
}
