


























































































































import Vue from "vue";
import $ from "jquery";
import Component from "vue-class-component";
import { Carousel } from "bootstrap";
import Swal from "sweetalert2";

@Component
export default class SectionHeader extends Vue {
  // INITIALIZATION
  private created() {
    $(document).ready(function () {
      const myCarousel = document.querySelector("#carouselProduct");
      if (myCarousel) {
        new Carousel(myCarousel, {
          slide: true,
          interval: 2500,
        });
      }
    });
  }

  private actionBuy() {
    Swal.fire({
      title: "Order via website is<br>not available at this time!",
      html: "<p>You can order via WhatsApp or<br/>come directly to the farm location.</p>",
      icon: "warning",
      showCancelButton: false,
      confirmButtonColor: "#3085d6",
      confirmButtonText: "Okay",
    });
  }
}
