











































































import Vue from "vue";
import $ from "jquery";
import Component from "vue-class-component";

@Component
export default class InvestorMenu extends Vue {
  // METHODS
  private isShow(attId: string) {
    return $(attId).hasClass("show");
  }

  private showDropdown(attId: string) {
    const attribut = attId + " ul.dropdown";
    const icon = attId + " i.icon";
    const isCurrentAtt = this.isShow(attribut);

    if (!isCurrentAtt) {
      $("i.icon").removeClass("bi-caret-down-fill");
      $("i.icon").addClass("bi-caret-right-fill");
      $(icon).removeClass("bi-caret-right-fill");
      $(icon).addClass("bi-caret-down-fill");
      $(".dropdown").removeClass("show");
      $(attribut).addClass("show");
    } else {
      $(icon).removeClass("bi-caret-down-fill");
      $(icon).addClass("bi-caret-right-fill");
      $(attribut).removeClass("show");
    }
  }
}
