  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private actionDetail(item: any) {
    Swal.fire({
      title: "Detail news is<br>not available at this time!",
      html: "News for " + item.title + " can you see on instagram!",
      icon: "warning",
      showCancelButton: false,
      confirmButtonColor: "#3085d6",
      confirmButtonText: "Okay",
    });
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private getLogo(item: any) {
    switch (item.image) {
      case "kambing":
        return require("@/assets/images/header_1.jpeg");
      case "ayam":
        return require("@/assets/images/header_2.png");
      case "entok":
